@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit;padding:0;margin:0}body{margin:0;min-width:320px;overflow-x:hidden;font-family:'Open Sans', sans-serif;font-weight:600;font-size:14px;line-height:21px;background-color:#F3F7FA;color:#4A4A4A;padding-bottom:50px}h1,h2,h3,h4,h5,h6,p{margin:0;padding:0}ul,li{margin:0;padding:0;list-style:none}a{color:inherit;display:inline-block;text-decoration:none}img{max-width:100%}.container{max-width:784px;padding:0 15px;margin:0 auto}.main{display:-webkit-flex;display:flex;-webkit-align-items:flex-start;align-items:flex-start}.main__inner{width:100%}.logo{text-align:center;padding:30px 0}.filters{background-color:#fff;border-radius:5px;box-shadow:0 2px 8px rgba(0,0,0,0.1);width:100%;margin-right:20px;max-width:232px;padding:20px 0 10px}.filters__head{text-transform:uppercase;margin-bottom:12px;letter-spacing:0.5px;font-size:12px;line-height:1;padding:0 20px}.filters__item.disabled{opacity:.6;cursor:default}.filters .checkbox{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:10px 20px;transition:background-color .2s;cursor:pointer}.filters .checkbox:hover{background-color:#F1FCFF}.filters .checkbox__real{width:1px;height:1px;position:absolute;opacity:0;-webkit-appearance:none;appearance:none}.filters .checkbox__real:checked ~ .checkbox__fake:after{opacity:1}.filters .checkbox__fake{position:relative;display:block;width:20px;height:20px;border:1px solid #9ABBCE;border-radius:2px;margin-right:10px}.filters .checkbox__fake::after{content:'';position:absolute;opacity:0;transition:opacity .2s;left:2px;top:4px;width:14px;height:8px;border-radius:1px;border-left:3.5px solid #2196f3;border-bottom:3.5px solid #2196f3;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.filters .checkbox__text{font-size:13px;line-height:20px}.tabs{width:100%;margin-bottom:20px;display:-webkit-flex;display:flex;text-transform:uppercase;font-size:12px;line-height:20px;letter-spacing:0.5px}.tabs__item{padding:15px;-webkit-flex:1 0 auto;flex:1 0 auto;text-align:center;cursor:pointer;background-color:#fff;transition:background-color .2s;-webkit-user-select:none;-ms-user-select:none;user-select:none;border-top:1px solid #DFE5EC;border-bottom:1px solid #DFE5EC;border-right:1px solid #DFE5EC}.tabs__item:first-child{border-top-left-radius:5px;border-bottom-left-radius:5px;border-left:1px solid #DFE5EC}.tabs__item:last-child{border-top-right-radius:5px;border-bottom-right-radius:5px}.tabs__item.active{background-color:#2196F3;color:#ffffff;border:none}.tabs__item.disabled{cursor:default;opacity:.6}.tickets__item{padding:17px 20px;background-color:#fff;margin-bottom:20px;border-radius:5px;box-shadow:0 2px 8px rgba(0,0,0,0.1)}.tickets .ticket__head{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;margin-bottom:20px}.tickets .ticket__price{font-size:24px;line-height:24px;color:#2196F3}.tickets .ticket__img img{width:110px;height:36px}.tickets .info__row{display:-webkit-flex;display:flex}.tickets .info__row:not(:last-child){margin-bottom:10px}.tickets .info__col{width:calc(100% / 3)}.tickets .info__col-head{font-size:12px;line-height:18px;letter-spacing:0.5px;text-transform:uppercase;color:#A0B0B9}.tickets__btn{width:100%;padding:15px 0;background-color:#2196F3;border-radius:5px;border:none;color:#ffffff;cursor:pointer;font-size:12px;line-height:20px;letter-spacing:0.5px;text-transform:uppercase;transition:background-color .2s;outline:none}.tickets__btn:hover{background-color:#1586e0}.loader{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-height:400px}.lds-ring{display:inline-block;position:relative;width:80px;height:80px}.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #3c70b3;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#2196F3 transparent transparent transparent}.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.error{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-height:320px}.error__text{margin-bottom:20px;font-size:20px;line-height:1}.error__btn{padding:12px 15px;cursor:pointer;background-color:#65bf63;border:none;border-radius:3px;color:#ffffff;outline:none;transition:background-color .2s}.error__btn:hover{background-color:#51a74f}

