.filters {
  $filter-padding: 20px;

  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  margin-right: 20px;
  max-width: 232px;
  padding: $filter-padding 0 10px;

  &__head {
    text-transform: uppercase;
    margin-bottom: 12px;
    letter-spacing: 0.5px;
    font-size: 12px;
    line-height: 1;
    padding: 0 $filter-padding;
  }

  &__item.disabled {
    opacity: .6;
    cursor: default;
  }

  .checkbox {
    display: flex;
    align-items: center;
    padding: 10px $filter-padding;
    transition: background-color .2s;
    cursor: pointer;

    &:hover {
      background-color: #F1FCFF;
    }

    &__real {
      width: 1px;
      height: 1px;
      position: absolute;
      opacity: 0;
      appearance: none;
    }

    &__real:checked ~ .checkbox__fake:after {
      opacity: 1;
    }

    &__fake {
      position: relative;
      display: block;
      width: 20px;
      height: 20px;
      border: 1px solid #9ABBCE;
      border-radius: 2px;
      margin-right: 10px;

      &::after {
        content: '';
        position: absolute;
        opacity: 0;
        transition: opacity .2s;
        left: 2px;
        top: 4px;
        width: 14px;
        height: 8px;
        border-radius: 1px;
        border-left: 3.5px solid #2196f3;
        border-bottom: 3.5px solid #2196f3;
        transform: rotate(-45deg);
      }
    }

    &__text {
      font-size: 13px;
      line-height: 20px;
    }

  }

}
